<template>
  <div id="app">
    
    <!-- Header -->
    <app-header/>

    <!-- Routher View -->
    <transition
      name="fade"
      mode="out-in">

      <router-view :key="$route.name"></router-view>
    </transition>

    <!-- Footer -->
    <transition
      name="fade"
      mode="out-in">
      <app-footer/>
    </transition>
    
  </div>
</template>

<script>
import Header from '@/components/Header'
import Footer from '@/components/Footer'
import store from './store'

export default {
  store,
  name: "App",
  components: {
    'app-header': Header, 
    'app-footer': Footer
  },
  created() {
    
  },
  watch: {
    $route (to, from) {
      if (to.name == 'portfolio detail') {
        $('#header').addClass('hide');
      } else {
        $('#header').removeClass('hide');
      }
    }
  }
};
</script>

